<template >
  <div class='SBoard' v-bind:style="{ background: '#'+info.color}">
  <div class='mar2 container'>
      <h1 style='text-align: center; margin-bottom: 30px; color: white;'>{{info.name}}</h1>


    <div class='row'>
        <div class='col-3' v-for='table in tables'>
          <div>
            <div class='table' v-if='!table.editing'>
              <h6>{{table.name}}</h6>
              <draggable
                class="dragArea list-group "
                :list="table.list"
                :clone="clone"
                :group="{ name: 'people', pull: pullFunction }"
                @start="start"
                @end='onEnd'
              >
              <div v-for='tableNote in table.list' class='tableNote' >

                {{tableNote.note}}

                <div v-if='tableNote.view'>
                  {{tableNote.description}}
                </div>

              </div>
            </draggable>
              <div class='addTNote' @click='table.editing = 1'>+ add one more</div>
            </div>
            <div class='addNote1' v-if='table.editing'>
              <div style='float:right; cursor:pointer;' @click='table.editing = 0'>&times;</div>
              <h5 style='text-align:center'>Create</h5>
              <input v-model='table.newNote' placeholder='name' class='input'>
              <input v-model='table.description' placeholder='description' class='inputD'>

              <button @click='postTableNote(table)'>Submit</button>
            </div>
          </div>
        </div>
      <div class='col-3'>
        <div class='table' style="background-color: ; ">
          <h6>Create one more</h6>
          <div v-if='!add' class='tableNote' @click='add=true' style="text-align: center; ">+</div>
          <div v-if='add'>
            <input v-model='name' placeholder='name' class='input'>
            <button @click='postTable'>Submit</button>
          </div>
        </div>
      </div>
    </div>

  </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
export default{
  components: {
    draggable
  },
  data(){
    return {
      trash: [
        {name:'move here to delete', color: 'e57676', id: 1}
      ],
      info: null,
      add: 0,
      name: '',
      link: '',
      id: null,
      tables: null,
      colorF: '',
      controlOnStart: true
    }
  },
  created(){
    this.id = this.$route.params.id
    this.link = '//kiwido.site/api/table/get/'
    this.link = this.link + localStorage.token +'/'+this.id + '/'
    this.axios.get(
      this.link
    ).then((response) => {

      this.tables = response.data
      this.add = false
    })
    this.link = '//kiwido.site/api/desk/info/'
    this.link = this.link + localStorage.token +'/'+this.id + '/'
    this.axios.get(
      this.link
    ).then((response) => {
      this.info = response.data
      this.add = false
    })
  },
  methods: {
    postTableNote(table){
      table.editing = 0
      this.link = '//kiwido.site/api/tableNote/post/'
      this.link = this.link + localStorage.token +'/'+this.id + '/' + table.newNote + '/' + table.description +'/' + 0 + '/' + 0 + '/' + table.id + '/'
      this.axios.get(
        this.link
      ).then((response) => {
        this.add = false
        document.location.reload(true)
      })
      document.location.reload(true)
    },
    postTable(){
      this.link = '//kiwido.site/api/table/post/'
      this.link = this.link + localStorage.token +'/'+this.id + '/' + this.name + '/' + 0 +'/'
      this.axios.get(
        this.link
      ).then((response) => {
        this.add = false
        document.location.reload(true)
      })
      document.location.reload(true)
    },

    pullFunction() {
      return this.controlOnStart ? "clone" : true;
    },
    start({ originalEvent }) {
      this.controlOnStart = originalEvent.ctrlKey;
      this.trashShow = true
    },
    onEnd(){
      for (var i in this.tables){
        for (var note in this.tables[i]['list']){
          this.link = '//kiwido.site/api/tableNote/edit/'
          this.link = this.link + localStorage.token +'/' + this.id + '/' + this.tables[i]['list'][note]['note'] + '/' + this.tables[i]['list'][note]['description'] + '/' + this.tables[i]['list'][note]['color'] + '/' + note + '/' + this.tables[i]['id'] + '/' + this.tables[i]['list'][note]['id'] + '/'
          this.axios.get(
            this.link
          ).then((response) => {
            this.link = ''
          })
        }
      }
    }
  },
}
</script>


